@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
body{font-family:'Lato', sans-serif;font-weight:700;background:#111;color:#fff;height:100%}.App{padding:30px;height:100vh;display:flex;flex-direction:column;background-color:#111}.App__container{text-align:center}.App__title{font-size:40px;margin-bottom:100px;text-align:center}.App__clock{padding-bottom:30px}.App__buttons{display:flex;grid-gap:20px;gap:20px;position:fixed;bottom:40px;right:0;left:0;margin:0 auto;width:-webkit-max-content;width:max-content}.App__button{padding:20px;border-radius:60px;border:2px solid #3a1888;background-color:#fff;font-family:inherit;cursor:pointer}.App__button:hover{border-color:#bb1993}

.Clock{padding:10px;font-size:80px;text-align:center;color:#f5f5f5;text-shadow:0px 0px 3px #000;-webkit-animation:color 1s infinite;animation:color 7s infinite}.Clock__name{font-size:40px;color:#fff}@-webkit-keyframes color{0%{color:#f5f5f5;text-shadow:0px 0px 3px #000}50%{color:#8343cc;text-shadow:0px 0px 3px #7700ff}}@keyframes color{0%{color:#f5f5f5;text-shadow:0px 0px 3px #000}50%{color:#8343cc;text-shadow:0px 0px 3px #7700ff}}

